.fscreen {
	background: $blue;
	height: 100vh;
	position: relative;
	&__title {
		@include vcenter;
		opacity: 0;
		@include lm {
			top: 56%; }
		position: relative;
		z-index: 1;
		text-align: center; }
	&__scene {
		opacity: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		@include trans;
		top: 0;
		left: 0; } }
